import React from 'react'
import { connect } from 'react-redux'
import { fetchUserData } from '../store'

const TestRemove = ({ data, stateData }) => {
    return (
        <div>
            {data.key}
            <h1>{stateData.url}</h1>
        </div>
    )
}

const testie = TestRemove.getInitialProps = async ({ reduxStore, state }) => {
    let res = await reduxStore.dispatch(fetchUserData())
    return {
        data: state.userData
    }
}

// const mapStateToProps = state => {
//     return {
//         stateData: state.userData
//     }
// }

export default connect(testie)(TestRemove)